<template>
    <div class="container">
        <div v-for="(item, idx) in list" v-bind:key="item.id">
            <a class="link" v-bind:href="item.url">{{item.name}}</a>
            <span v-if="idx != list.length - 1"> | </span>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'LinkBox',
        props: {
            list: {
                type: Array,
                default: () => {return []}
            }
        }
    }
</script>

<style scoped>
	.container {
		display: flex;
	}

	.link {
		color: #0088CC;
		margin: 0 2px 0 5px;
	}
</style>